<template>
    <div>
        <h2>詳細情報</h2>
        <p v-if="itemData">アイテム名: {{ itemData.Name }}</p>
        <p v-if="itemData">レベル: {{ itemData.level }}</p>
        <p v-if="itemData">タイプ: {{ itemData.type }}</p>
        <p v-if="itemData">メインオプション: {{ itemData.Main_Option }}</p>
        <p v-if="itemData">サブオプション: {{ itemData.Sub_Option }}</p>
        <p v-if="itemData">効果: {{ itemData.Effect }}</p>
        <img v-if="itemData" :src="itemData.Image" alt="アイテム画像" width="100" height="100">
        <p v-else>アイテムが見つかりません</p>
    </div>
</template>
  
<script>
// echos.jsonをインポート
import echosData from '../echos.json'; // 実際のパスに置き換える

export default {
    data() {
        return {
            itemData: null
        };
    },
    created() {
        // route.params.itemName からアイテム名を取得
        const itemName = this.$route.params.itemName;

        // JSONデータからアイテムを検索
        const foundItem = echosData.find((item) => item.Name === itemName);

        if (foundItem) {
            // アイテムが見つかった場合は詳細情報を設定
            this.itemData = foundItem;
        } else {
            // アイテムが見つからなかった場合の処理
            console.error('アイテムが見つかりません');
        }
    }
};
</script>